  public getMetricValues() {
    return new Promise(resolve => {
      this.http.get("https://api.neiru.org/get-all-metric-values.php", { headers: this.restProvider.headers })
        .subscribe(data => {
          //------------------TEMPORRIO-------------------
          data = this.resolveItens(data);
          //------------------TEMPORRIO-------------------
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

