import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { Storage } from '@ionic/storage';
import { QuestionaryProvider } from '../../providers/questionary/questionary';
import { CityProvider } from '../../providers/city/city';
import { PlanProvider } from '../../providers/plan/plan';
import { DatabaseProvider } from '../../providers/database/database';
import { RestProvider } from '../../providers/rest/rest';

@IonicPage()
@Component({
  selector: 'page-intro',
  templateUrl: 'intro.html',
})
export class IntroPage {
  loader: any;

  constructor(private cityProvider: CityProvider, private planProvider: PlanProvider, private questionaryProvider: QuestionaryProvider, public loadingCtrl: LoadingController, public navCtrl: NavController, public navParams: NavParams, private databaseProvider: DatabaseProvider, private storage: Storage, private alertCtrl: AlertController, public restProvider: RestProvider) {
      this.navigateHomePage();
  }

  navigateHomePage() {
    this.storage.get('intro')
      .then((data) => {
        if (data != null) {
          this.navCtrl.setRoot(HomePage, {});
        }
      }).catch(() => console.log('error setting intro'));
  }

  skip() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.storage.set('intro', true).then(() => {
        this.navCtrl.setRoot(HomePage, {}).then(this.loader.dismiss());
    });
  }
}



