import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Plan } from "../plan/plan";
import { Question } from "../question/question";
import { RestProvider } from "../rest/rest";
import { Respondent } from "../../pages/respondent-profile/respondent-profile";
import { Neighborhood } from "../neighborhood/neighborhood";

@Injectable()
export class QuestionaryProvider {

  constructor(public http: HttpClient, public restProvider: RestProvider) {
  }

  public getAllQuestionariesByPlan(plan) {
    return new Promise(resolve => {
      this.http.get("https://api.neiru.org/get-all-questionaries-by-plan.php?plan=" + plan.id, { headers: this.restProvider.headers })
        .subscribe(data => {
         
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

  public getAllPlanQuestionariesAnsweredByRespondent(plan: Plan, questionaries: Questionary[], respondent: Respondent) {
    return new Promise(resolve => {
      let planJson = {
        "plan_id": plan.id
      };

      let respondentJson = {
        "respondent_id": respondent.id
      };

      let questionariesJson = [];
      questionaries.forEach((questionary) => {
        let item = {
          "questionary_id": questionary.id,
          "questionary_name": questionary.name
        };
        questionariesJson.push(item);
      });

      let data = {
        "plan": planJson,
        "questionaries": JSON.parse(JSON.stringify(questionariesJson)),
        "respondent": respondentJson
      };
      this.http.post("https://api.neiru.org/get-all-plan-questionaries-answered-by-respondent.php", data, { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

  public resolveQuestionaryIcon(questionaries) {
    let questionariesParse = [];
    questionaries.forEach((questionary) => {
      let item = {
        "id": questionary.id,
        "name": questionary.name,
        "answered": questionary.answered,
        "icon": "city-icon.png"
      };
      switch (item.name) {
        case "Segurana Pblica": {
          if (item.answered) {
            item.icon = "security-icon-selected.png";
          } else {
            item.icon = "security-icon.png";
          }
          break;
        }
        case "Mobilidade e Transporte": {
          if (item.answered) {
            item.icon = "urban_traffic-icon-selected.png";
          } else {
            item.icon = "urban_traffic-icon.png";
          }
          break;
        }
        case "Uso e Ocupao do Solo": {
          if (item.answered) {
            item.icon = "environment-icon-selected.png";
          } else {
            item.icon = "environment-icon.png";
          }
          break;
        }
        case "Sade": {
          if (item.answered) {
            item.icon = "health-icon-selected.png";
          } else {
            item.icon = "health-icon.png";
          }
          break;
        }
        case "Lazer Cultura e Turismo": {
          if (item.answered) {
            item.icon = "culture-icon-selected.png";
          } else {
            item.icon = "culture-icon.png";
          }
          break;
        }
        case "Saneamento": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        case "Educao": {
          if (item.answered) {
            item.icon = "education-icon-selected.png";
          } else {
            item.icon = "education-icon.png";
          }
          break;
        }
        case "Meio Ambiente": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        case "Abastecimento de gua": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        case "Esgotamento Sanitrio": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        case "Drenagem Urbana": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        case "Resduos Slidos": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        case "Informaes Adicionais": {
          if (item.answered) {
            item.icon = "water_residues-icon-selected.png";
          } else {
            item.icon = "water_residues-icon.png";
          }
          break;
        }
        default: {
          if (item.answered) {
            item.icon = "city-icon-selected.png";
          } else {
            item.icon = "city-icon.png";
          }
          break;
        }
      }
      questionariesParse.push(item);
    });
    return questionariesParse;
  }
}

export class Questionary {
  id: number;
  name: string;
  plan: Plan;
  questions: Question[];
  answered: boolean;
}


export class Answer {
  plan: Plan;
  questionary: Questionary;
  question: Question;
  respondent: Respondent;
  answer: number;
  created_at: string;
  isCompleted: boolean;
}

export class AnswerNeighborhood {
  plan: Plan;
  questionary: Questionary;
  question: Question;
  respondent: Respondent;
  neighborhood: Neighborhood;
  created_at: string;
}



